<script>
export default {
  name: "UsersRegister",
  data () {
    return {
      user: {
        "username": "",
        "email": "",
        "email2": "",
        "password": "",
        "password2": "",
        "nameFirst":"",
        "nameLast":"",
        "street":"",
        "street2":"",
        "zip":"",
        "city":"",
        "state":"",
        "country":"",
        "phone":""
      }
    }
  },
  created: function () {
    //this.getUsers();
  },
  methods: {
    userRegister: function() {
      let readyToSend = false;
      let fieldsPassed = 0;
      let requiredFields = ["username", "email", "password"];//, "nameFirst", "nameLast", "street", "zip", "city", "country", "phone"];
      let fieldsToConfirm = ["email", "password"];
      requiredFields.forEach((field) => {
        if ( typeof this.user[field] !== "undefined" && this.user[field]!=null && this.user[field].toString().trim()!="" ) { // check if not empty
          if ( fieldsToConfirm.indexOf(field)>-1 ) {
            if ( this.user[field]===this.user[field+"2"] ) {
              fieldsPassed++;
            }
          } else {
            fieldsPassed++;
          }
        }
      });
      if ( fieldsPassed==requiredFields.length ) {
        readyToSend = true;
      }
      if ( readyToSend ) {
        this.$httpc.post("users/register",{
          user: {
            username: this.user.username,
            email: this.user.email,
            password: this.user.password
          }
        }, {
            timeout: 5000
        })
          .then(function (response) {
            // handle success
            console.log(response.data);
          })
          .catch(function (error) {
            // handle error
            console.log(error);
          })
          .then(function () {
            // always executed
          });
      }
      console.log(readyToSend);
    }
  }
}
</script>

