<template>
  <div class="container users-login">
    <div class="column">
      <h1 class="title is-1">Login</h1>
    </div>
    <div class="column is-4 is-offset-4">
      <form v-on:submit.prevent="userLogin" ref="loginSubmit">
        <div class="field email">
          <div class="control">
            <input class="input" placeholder="Email" type="email" v-model.trim="email" />
          </div>
          <div class="form-messages is-hidden">
            <div class="form-message" data-message="wrong credentials" translate="yes">Wrong credentials</div>
          </div>
        </div>
        <div class="field password">
          <div class="control">
            <input class="input" placeholder="Password" type="password" v-model.trim="password" />
          </div>
        </div>
        <div class="field">
          <span translate="yes">Remember my login on this computer</span>
          <input class="checkbox" type="checkbox" v-model="remember" />
        </div>
        <button class="button is-medium is-primary" type="submit" translate="yes">Login</button>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "UsersLogin",
  data () {
    return {
      email: "",
      password: "",
      remember: false
    }
  },
  created: function () {
    //this.getUsers();
  },
  methods: {
    userLogin: function() {
      let self = this;
      this.$httpc.post("users/login",{
        user: {
          email: this.email,
          password: this.password
        }
      })
        .then(function (response) {
          // handle success
          console.log(response);
          if ( response.data.user.token ) {
            self.$store.state.user.logged = true;
            self.$store.state.user.token = response.data.user.token;
            self.$store.state.user.remember = self.remember;
            let profile = response.data.user;
            self.$store.dispatch("setUser", profile);
            if ( self.remember==true ) {
              localStorage.setItem("token", response.data.user.token);
            } else {
              sessionStorage.setItem("token", response.data.user.token);
            }
            self.$router.push({"name": "me"});
          }
        })
        .catch(function (error) {
          // handle error
          if ( error.response && error.response.data && error.response.data.errors && Object.keys(error.response.data.errors).length>0 ) {
            // decorate form fields with errors
            Object.keys(error.response.data.errors).forEach(function (item) {
              let element = self.$el.querySelector('.field.'+item);
              if ( element ) {
                let messageElement = element.querySelector('.form-messages .form-message[data-message="'+error.response.data.errors[item]+'"]');
                console.log('.field.'+item, '.form-messages .form-message[data-message="'+error.response.data.errors[item]+'"]', messageElement);
                if (messageElement && messageElement!=null) {
                  self.$helpers.undecorateFormField(element, 'danger');
                  let message = messageElement.innerText;
                  self.$helpers.decorateFormField(element, message, 'danger');
                }
              }
            });
          }
        })
        .then(function (response) {
          // always executed
        });
    }
  }
}
</script>

